
/**
 * Создает GUID
 * Взял его, т.к. такой формат айдишек у оракла
 */
export function createGuid() {
    function s4 () {
        return Math.floor((1 + Math.random()) * 0x10000)
            .toString(16)
            .substring(1)
    }
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4()
}